<template>
  <!-- 布局容器 -->
  <el-container>
    <!-- 侧边栏 -->
    <el-aside width="220px">
      <!-- 导航栏 -->
      <el-menu
        unique-opened
        router
        :default-active="defaultActivie"
        class="el-menu-vertical-demo"
        background-color="#304152"
        text-color="#fff"
        active-text-color="#3f9af7"
      >
        <h4 class="title"><img src="@/assets/imgs/favicon.png" style="width:40px;vertical-align: middle;"> 小鱼后台管理</h4>
        <!-- 导航栏循环创建 -->
        <div v-for="item in userGroup" :key="item.index">
          <!-- 如果有children，就渲染有子菜单的 -->
          <el-submenu v-if="item.children" :index="item.index">
            <template slot="title">
              <i :class="item.cls"></i>
              <span>{{item.title}}</span>
            </template>
            <el-menu-item-group>
              <!-- 循环渲染子菜单 -->
              <el-menu-item
                v-for="child in item.children"
                :key="child.index"
                :index="child.index"
              >{{child.title}}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <!-- 否则渲染没有子菜单的 -->
          <el-menu-item v-else :index="item.index">
            <i :class="item.cls"></i>
            <span slot="title">{{item.title}}</span>
          </el-menu-item>
        </div>
      </el-menu>
    </el-aside>
    <!-- 右边内容 -->
    <el-container class="indexRight">
      <!-- 头部 -->
      <el-header class="clearfix">
        <div class="breadpath">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item
              v-for="item in currentPath"
              :to="item.path"
              :key="item.title"
            >{{item.title}}</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <!-- 注销登录/个人中心 -->
        <div class="head">
          <el-dropdown @command="handleCommand">
            <!--command事件，触发点击对应key的事件-->
            <span class="el-dropdown-link">
              {{username}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="myInfo" icon="el-icon-s-custom">个人中心</el-dropdown-item>
              <el-dropdown-item command="editPwd" icon="el-icon-edit">修改密码</el-dropdown-item>
              <el-dropdown-item command="logOut" icon="el-icon-switch-button">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- 头像 -->
          <div class="demo-basic--circle">
            <div class="block">
              <router-link to="/index/Myinfo">
                <el-avatar :src="circleUrl"></el-avatar>
              </router-link>
            </div>
          </div>
        </div>
      </el-header>
      <!-- 内容 -->
      <el-main>
        <!-- 路由出口 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
// 引入接口
import { API_CHECK_TOKEN, API_GET_MYINFO } from "@/API/apis.js";
export default {
  data() {
    return {
      // 面包屑导航
      currentPath: [{ title: "首页", path: "/index/Home" }],
      // 用户名
      username: "",
      // 头像地址
      circleUrl: localStorage.head,
      //默认激活指定导航栏
      defaultActivie: "",
      // 树菜单数据
      // index：index cls：图标 title：标题 children：二级子菜单下的内容  rele：有权限获取该内容的用户组
      treelist: [
        // 后台首页
        {
          index: "/index/Home",
          cls: "el-icon-s-home",
          title: "后台首页",
          role: ["normal", "super"]
        },
        // 订单管理
        {
          index: "/index/order",
          cls: "el-icon-s-order",
          title: "订单管理",
          role: ["normal", "super"]
        },
        // 商品管理一级菜单
        {
          role: ["normal", "super"],
          index: "1",
          cls: "el-icon-s-goods",
          title: "商品管理",
          children: [
            // 商品列表
            {
              index: "/index/Shoplist",
              title: "商品列表"
            },
            // 商品添加
            {
              index: "/index/Addshops",
              title: "商品添加"
            },
            // 商品分类
            {
              index: "/index/Shopclassify",
              title: "商品分类"
            }
          ]
        },
        // 店铺管理
        {
          index: "/index/StoreMana",
          cls: "el-icon-s-shop",
          title: "店铺管理",
          role: ["super"]
        },
        // 账号管理一级菜单
        {
          role: ["super"],
          index: "2",
          cls: "el-icon-user",
          title: "账号管理",
          children: [
            //账号列表
            { index: "/index/Userlist", title: "账号列表" },
            // 添加账号
            { index: "/index/Adduser", title: "添加账号" },
            // 修改密码
            {
              index: "/index/Editpwd",
              title: "修改密码"
            },
            // 个人中心
            {
              index: "/index/Myinfo",
              title: "个人中心"
            }
          ]
        },
        // 销售统计一级从菜单
        {
          role: ["super"],
          index: "3",
          cls: "el-icon-pie-chart",
          title: "销售统计",
          children: [
            //商品统计
            { index: "/index/Shopstatistics", title: "商品统计" },
            //订单统计
            { index: "/index/Orderstatistics", title: "订单统计" }
          ]
        }
      ],
      //当前登录用户的权限
      role: localStorage.role
    };
  },
  methods: {
    // 改变当前路由方法
    changeCurrentPath(hash) {
      let arr = [];
      switch (
        hash //switch  case 根据传入的参数的不同状态,执行不同的操作
      ) {
        case "/index/Home":
          arr = [{ title: "首页", path: "/index/Home" }];
          break;
        case "/index/order":
          arr = [{ title: "订单管理", path: "/index/order" }];
          break;
        case "/index/Shoplist":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "商品管理", path: "" },
            { title: "商品列表", path: "/index/Shoplist" }
          ];
          break;
        case "/index/Addshops":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "商品管理", path: "" },
            { title: "商品添加", path: "/index/Addshops" }
          ];
          break;
        case "/index/Shopclassify":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "商品管理", path: "" },
            { title: "商品分类", path: "/index/Shopclassify" }
          ];
          break;
        case "/index/StoreMana":
          arr = [{ title: "店铺管理", path: "/index/StoreMana" }];
          break;
        case "/index/Userlist":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "账号管理", path: "" },
            { title: "账号列表", path: "/index/Userlist" }
          ];
          break;
        case "/index/Adduser":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "账号管理", path: "" },
            { title: "添加账号", path: "/index/Adduser" }
          ];
          break;
        case "/index/Editpwd":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "账号管理", path: "" },
            { title: "修改密码", path: "/index/Editpwd" }
          ];
          break;
        case "/index/Myinfo":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "账号管理", path: "" },
            { title: "个人中心", path: "/index/Myinfo" }
          ];
          break;
        case "/index/Shopstatistics":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "销售统计", path: "" },
            { title: "商品统计", path: "/index/Shopstatistics" }
          ];
          break;
        case "/index/Orderstatistics":
          arr = [
            { title: "首页", path: "/index/Home" },
            { title: "销售统计", path: "" },
            { title: "订单统计", path: "/index/Orderstatistics" }
          ];
          break;
      }
      // 改变当前路径
      this.currentPath = arr;
    },
    // 右上角账号下拉菜单 command 点击对应按钮传入的参数
    handleCommand(command) {
      switch (command) {
        case "myInfo": //根据不同参数触发对应事件
          this.$router.push("/index/Myinfo");
          break;
        case "editPwd":
          this.$router.push("/index/Editpwd");
          break;
        case "logOut":
          this.$confirm("确认退出当前账号?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }).then(() => {
            localStorage.removeItem("acc");
            localStorage.removeItem("token");
            localStorage.removeItem("id");
            this.$router.push("/");
            this.$message({
              type: "success",
              message: "已退出当前账号!",
              duration: 1500
            });
          });
          break;
      }
    }
  },
  //监听hash变化
  watch: {
    $route: {
      //监听当前路由， newpath：新的hash地址
      handler: val => {
        window._indexthis.changeCurrentPath(val.path);
      },
      deep: true //深度监听
    }
  },
  computed: {
    //计算属性
    //根据用户所在组权限,进行不同的导航创建
    userGroup() {
      return this.treelist.filter(i => i.role.includes(this.role)); //究极缩写
    }
  },
  // userGroup() {
  //   return this.treelist.filter(i => {
  //     return i.role.includes(this.role);
  //   });
  // }

  created() {
    // data,组件初始化完毕后执行以下代码
    let hash = this.$route.path; //保存当前hash
    this.defaultActivie = hash; //默认激活hash
    this.changeCurrentPath(hash); //刷新后重新激活
    // 保存this
    window._indexthis = this;
    // 获取用户的权限
    //this.role = localStorage.role;
    // 验证用户token时效
    API_CHECK_TOKEN(localStorage.token).then(res => {
      //console.log(res);
      // token已过期，需重新登录
      if (res.data.code == 1) {
        this.$alert("身份验证已过期，请重新登录", "提示", {
          confirmButtonText: "确定",
          // 不管点击什么都会跳转到登录页面
          callback: action => {
            if (action) {
              localStorage.removeItem("id"); //清空本地id
              localStorage.removeItem("token"); //清空本地token
              location.href = "/";
            }
          }
        });
      } else {
        // 在右上角显示用户名
        this.username = localStorage.acc;
        API_GET_MYINFO(localStorage.id).then(res => {
          this.circleUrl = res.data.accountInfo.imgUrl;
          // console.log(res);
        });
      }
    });

    // 防止用户修改密码跳转到首页后，通过"后退"回到管理系统
    //获取本地存储的id
    // if (localStorage.getItem("id") == null) {
    // if (localStorage.getItem("token") == null) {
    //   //获取本地存储的token
    //   this.$alert("您还未登录", "提示", {
    //     confirmButtonText: "确定",
    //     // 用户点击按钮后的回调函数
    //     callback: action => {
    //       //action：有三个值，confirm，close，cancel
    //       if (action) {
    //         //不管点什么都会跳转到登录
    //         location.href = "/";
    //       }
    //     }
    //   });
    // }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
@basecolor2: #304152;

.el-container {
  height: 100%;
  .el-aside {
    background-color: @basecolor2;

    .el-menu {
      border: none;
      h4 {
        color: #fff;
        text-align: center;
        margin-top: 10px;
        margin-bottom: 0;
      }
    }
  }

  .indexRight {
    background-color: #f2f0f5;
    background: url(../assets/imgs/bckimg.jpg) no-repeat;
    .el-header {
      margin-bottom: 10px;
      height: 50px;
      background-color: #fff;
      display: flex;
      justify-content: space-between;
      background-color: #cfe9fa;

      box-shadow: 0 0 5px 10px #cfe9fa;
      .head {
        line-height: 50px;
        margin-right: 20px;
        display: flex;
        .block {
          display: flex;
          padding-top: 8px;
          margin-left: 5px;
        }
        .el-dropdown-link {
          color: rgb(25, 117, 238);
        }
      }
      .breadpath {
        > div {
          line-height: 60px;
          flex: 5;
        }
      }
      a {
        text-decoration: none;
      }
      .spanMlr {
        margin-left: 5px;
        margin-right: 5px;
      }
    }
    .el-main {
      padding: 10px;
    }
  }
}
</style>